<template>
	<view>
		<cu-custom bgColor="bg-blue-one" :isBack="false" :isR="false">
			<block slot="content">商场</block>
		</cu-custom>
		<view class="cu-bar search bg-white fixed" :style="[{top:CustomBar + 'px'}]">
			<view class="search-form round">
				<text class="cuIcon-search"></text>
				<input @focus="InputFocus" @blur="InputBlur" :adjust-position="false" type="text" placeholder="搜索关键词" confirm-type="search"></input>
			</view>
			<view class="action">
				<button class="cu-btn bg-blue-one shadow-blur round">搜索</button>
			</view>
		</view>
		<!-- 轮播图 -->
		<view class="swiper" style="margin-top: 80px;">
			<view class="swiper-box">
				<swiper circular="true" autoplay="true" @change="swiperChange">
					<swiper-item v-for="swiper in swiperList" :key="swiper.id">
						<image :src="swiper.img"  ></image>
					</swiper-item>
				</swiper>
				<view class="indicator">
					<view
						class="dots"
						v-for="(swiper, index) in swiperList"
						:class="[currentSwiper >= index ? 'on' : '']"
						:key="index"
					></view>
				</view>
			</view>
		</view>
		<!-- 商品列表 -->
		<view class="goods-list margin-bottom">
			<view class="title">
				<image src="/static/hua.png"></image>
				猜你喜欢
				<image src="/static/hua.png"></image>
			</view>
			<view class="product-list">
				<view
					class="product"
					v-for="product in productList"
					:key="product.goods_id"
					@tap="toGoods(product)"
				>
					<image mode="widthFix" :src="product.img"></image>
					<view class="name">{{ product.name }}</view>
					<view class="info">
						<view class="price">{{ product.price }}</view>
						<view class="slogan">{{ product.slogan }}</view>
					</view>
				</view>
			</view>
			<view class="loading-text">{{ loadingText }}</view>
		</view>
	</view>
</template>

<script>
var ttt = 0;

export default {
	data() {
		return {
			CustomBar: this.CustomBar,
			currentSwiper: 0,
			// 轮播图片
